import { API } from '@/api'
import { defineStore } from 'pinia'

export default defineStore('user', {
    state: () => {
        return {
            user: null,
        }
    },
    actions: {
        getUser(data) {
            return new Promise(resolve => {
                API.signIn(data).then(response => {
                    this.user = response.data
                    resolve(true)
                })
            })
        },
    }
})